/*  src/Title.css  */
.Title {
  box-sizing: border-box;
  width: 100%;
  justify-content: center;
  padding: 1em;
  margin-bottom: 2em;
  background-color: rgb(192, 45, 26);
  color: #fff;
}

.Title-Subtitle {
  color: rgba(255, 255, 255, 0.75);
  padding-top: 0;
}

.Title .nav-link{
  display: inline-block;
  padding: 0.5em 1em;
  color: 'yellow';
  font-weight: bold;
  text-decoration: none;
}

.Title .nav-link-active {
  color: #fff;
  border-bottom: 2px solid;
}
