.CardDetails {
	display: flex;
	flex-direction: row-reverse;
	width: 960px;
	margin: auto;
}

.CardDetails .CardDetails-info {
	flex: 1;
	text-align: left;
	padding: 0 1em;
}

.CardDetails .CardDetails-info .CardDetails-title {
	font-size: 2em;
	margin: 0;
}

.CardDetails .CardDetails-image {
	flex: 2;
}

.CardDetails .CardDetails-image img {
	width: 100%;
}

.CardDetails .CardDetails-hours {
	font-size: 1.5em;
}